.footer-container {
   background: linear-gradient(90deg, #024873, #028090);
  color: #ffffff;
  padding: 40px 20px 20px;
  font-family: 'Segoe UI', sans-serif;
}

.footer-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1200px;
  margin: auto;
}

/* ✅ Left Logo Box */
.footer-logo-box {
  flex: 1;
  min-width: 200px;
  margin: 20px;
  background-color: #ffffff;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.footer-logo {
  height: 100px;
  margin-bottom: 10px;
}

.footer-logo-text {
  color: #024873;
  font-weight: 600;
  font-size: 14px;
}

/* ✅ Middle and Right Sections */
.footer-section {
  flex: 1;
  min-width: 250px;
  margin: 20px;
}

.footer-section h3 {
  color: #ffffff;
  margin-bottom: 15px;
}

.footer-section ul {
  list-style: none;
  padding: 0;
}

.footer-section ul li {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}

.footer-section ul li a {
  color: #dddddd;
  text-decoration: none;
  transition: color 0.3s ease;
}

.footer-section ul li a:hover {
  color: #ffffff;
  text-decoration: underline;
}

/* ✅ Icon Styles */
.footer-icon {
  margin-right: 8px;
  color: #ffffff;
}

/* ✅ Bottom Text */
.footer-bottom {
  text-align: center;
  margin-top: 30px;
  font-size: 14px;
  border-top: 1px solid #eee;
  padding-top: 15px;
  color: #ccc;
}

/* ✅ Responsive */
@media (max-width: 768px) {
  .footer-content {
    flex-direction: column;
    align-items: center;
  }

  .footer-section,
  .footer-logo-box {
    text-align: center;
    margin: 20px 0;
  }
}
