/* components/CowCare.css */

.cow-container {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  padding: 30px;
  align-items: flex-start;
  justify-content: center;
}

.cow-left img {
  width: 700px;
  height: auto;
  border-radius: 10px;
  object-fit: cover;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.cow-right {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  max-width: 650px;
}

.medicine-card {
  width: 150px;
  background: #f8f8f8;
  border-radius: 10px;
  padding: 12px;
  text-align: center;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.medicine-card:hover {
  transform: scale(1.05);
}

.medicine-card img {
  width: 150px;
  height: 150px;
  object-fit: contain;
  margin-bottom: 8px;
}

.medicine-card p {
  font-size: 14px;
  font-weight: 500;
  color: #333;
}
